.card {
   width: 250px;
   border: 1px solid black;
   border-radius: 8px;
   overflow: hidden;
}
.card img {
   width: 100%;
}
.card h3,
.card p {
   margin: 6px 0px;
}
.card-content {
   padding: 1rem;
}
.container {
   display: flex;
   flex-wrap: wrap;
   gap: 1rem;
   max-height: 100px;
}
